:root{
    --blue:rgb(34,193,195);
    --vblue:rgb(19, 136, 138);
    --yellow:rgba(253,187,45,1);
    --brown:rgb(56 37 0);
    --white : rgb(250 249 247) ;
}
body{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: rgb(0,0,0);
    color: white;
    overflow-x: hidden;
    /* background: rgb(225 219 213); */
}

.outer{
    height: auto;
    background-color: white;
    flex-direction: column;
    text-rendering: optimizeLegibility;
    background-color: rgb(0,0,0);
    color: white;
}
.Hline{
    width: 105%;
    border-bottom: solid 3px var(--yellow);
    height: 90px;
    animation:  DVline 2.5s linear;
}
@keyframes DVline{
    0%{
        width: 0%;
    }
    100%{
        width: 100%;
    }
}
.Vline{
    width: 20%;
    height: 110vh;
    position: absolute;
    top: 0;
    z-index: 300;
    left: 100px;
    border-left: solid 3px var(--yellow);
    animation: DHline 2.5s linear;
}
@keyframes DHline{
    0%{
        height: 0%;
    }
    100%{
        height: 100%;
    }
}
.Hbox,.wave{
    max-width: 1316px;
    margin: auto;
    position: relative;
    width: 100%;
    height: 600px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    justify-content: space-evenly;
}
.Hbox{
    overflow: hidden;
    word-spacing: 1px;
    flex-shrink: 3;
    left: 56px;
}
.wave{
    max-width: 600px;
    width: 100%;
}
/* .Himg{
    position: relative;
    top: 50px;
    left: 80px;
} */
.Himg{
    width: 100%;
    height: 100%;
    position: relative;
    top: -50px;
    display: flex;
    align-items: center;
    justify-content: center;
    left: 110px;

}
.Himg img{
    width: 60%;
    height:60%;
    z-index: 40;
}
.Htxt{
    width: 49%;
    height: 55%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
}
.Htxt h2{
    font-size: 2.6rem;
}
.Htxt h6{
    font-size: 1rem;
}
.Htxt h5{
    font-size: 1.5rem;
}
  .Hwave,.rWave{
    max-width: 500px;
    position: absolute;
    top: 100px;
    right: 20px;
    width: 100%;
    height: 400px;
    box-sizing: border-box; 
    display: flex;
    transform: rotate(40deg);
    z-index: 30;
}

.svg1{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    transform: scale3d(7,5,2);
    /* background: rgb(238,174,202);
background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%); */
}

.outer svg path {
    width: 100%;
}

.fillW{
    position: absolute; 
    top: 20px;
    right: 20px;
    width: 350px;
    transform: rotate(45deg);
    z-index: 20;
}
.svg2{
    transform: scale3d(3,4,3);
}
.cIcon{
    display: none;
}
.dBtn{
    z-index: 500;
    padding: 12px 45px !important;
    color: var(--yellow) !important;
    letter-spacing: 1.2px !important;
    font-size: 1.1rem !important;
    margin-top: 20px !important;
    border-color: var(--yellow) !important;
}
.dBtn:hover{
    color: var(--blue) !important;
    border-color: var(--blue) !important;
}
.Harrow{
    max-width: 300px;
    width: 100%;
    position: relative;
}
.Harrow img {
    position: absolute;
    width: 100%;
    top: -20px;
    left: 155px;
    transform: rotate(-130deg);
}
@media (max-width : 1300px){
    .Hbox{
        left: 120px;
    }
    .Htxt h2{
        font-size: 2.3rem;
    }
    .Hline{
        width: 117%;
    }
}

@media (max-width:1000px){
    .Htxt{
        width: 60%;
    }   
    .Htxt h6{
        font-size: 0.8rem;
    }
    .Htxt h5{
        font-size: 1.1rem;
    }
}
@media (max-width:750px){
    .Hbox{
        flex-wrap: wrap;
        height: 800px;
        justify-content: center;
    }
    .Htxt{
        height:30%;
        width: 100%;
    }
}
@media (max-width:500px){
    .Himg{
        width: 100%;
        left: 50px;
        top: -10px;
    }
    .Vline{
        left: 50px;
        height: 175vh;
    }
    .Hline{
        height: 50px;
    }
    .Hbox{
        left: 50px;
    }
    .Htxt h2{
        font-size: 2rem;
    }
    .Htxt h6{
        font-size: 0.7rem;
    }
    .Htxt h5{
        font-size: 1rem;
    }
    .wave{
        height: 450px;
    }
    .Harrow img {
        width: 55%;
        top: 9px;
        left: 170px;
    }
}