
.oCpc{
    align-items: center;
    height: 600px;
    justify-content: space-around;
    z-index: 20;
    position: absolute;
    width: 100%;
    top: 0;
}
.imgCpc{
    max-width: 500px;
    height: 300px;
}

.pprCpc,.pprCpc2{
    width: 100px;
    height: 100px;
    position: relative;
    box-sizing: border-box;
    background-color: var(--vblue);
    background: url('../Images/ts/paper.png') no-repeat;
    background-size: 100% 100%;
    transform: rotate(270deg);
}
.pprCpc{
    animation: lMove 6s ease;
    animation-iteration-count: infinite;
    z-index: 10;
}
.pprCpc2{
    left: 45%;
    top: 200px;
    animation: lMove2 6s ease-out;
    animation-iteration-count: infinite;
}
@keyframes lMove{
    0%{
        left: 0%;
        top: 20px;
    }
    20%{
        left: 43%;
        top: 20px;
        transform: rotate(0deg);
    }
    50%{
        left: 48%;
        top: 250px;
    }
    100%{
        top: 250px;
        left: 100%;
    }
}
@keyframes lMove2 {
    0%{
        top: 190px;
        left: 46%;
    }
    50%{
        left: 48%;
        top: 230px;
        transform: rotate(0deg);
    }
    100%{
        top: 250px;
        left: 100%;
    }
}
.imgCpc img{
    width: 100%;
    height: 100%;
}
.cpcCode{
    position: relative;
    top: 300px;
    width: 100%;
    text-align: justify;
}
.oCode{
    width: 100%;
    align-items: center;
    justify-self: center;
    flex-direction: column;
    white-space: pre;
    line-height: 1.5;
    word-break: normal;
    padding: 20px;
    word-spacing: normal;
}
.oCode h2{
    margin-bottom: 20px;
}
.mCode{
    margin-top: 10px;
    color: #f2f2f2d3;
    background-color: rgba(0, 0, 0, 0.795);
    max-width: 900px;
    height: 500px;
    width: 100%;
    text-align: left;
    overflow: scroll;
    border-radius: 5px;
}
.btnCode{
   margin-top: 5px;
}
.btnCode button{
    letter-spacing: 1px;
    padding: 8px 25px;
}