@import url('https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap');

.root{
      background-color: var(--blue);
      font-family: 'Permanent Marker', cursive;
}
body{
    background-color: var(--blue);
}
.rdOuter{
    height: 100vh;
    overflow: hidden;
}
.Road{
    z-index: inherit;
    background-color: var(--white);
    height: 200px;
}
.rdInnr{
 max-width: 1600px;
 width: 100%;
 position: absolute;
 height: 300px;
 top: 200px;
 background-color: var(--white);
 z-index: 100;
 border-bottom-left-radius: 50%;
 border-bottom-right-radius: 50%;
 align-items: center;
 justify-content: space-around;
}
.rdInnr svg{
    z-index: 500;
    font-size: 6rem !important;
    color: var(--vblue) !important;
    border-radius: 5px !important;
}

.rdCircle{
    max-width: 1600px;
    width: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    align-items: center;
    justify-content: center;
    height: 300px;  
    background-color: var(--vblue);
    border-bottom-left-radius: 50%;
    border-bottom-right-radius: 50%;
    z-index: 200;
}
.fSt,.lSt{
    position: absolute;
    max-width: 100px;
    height: 100px;

}

.fSt img , .lSt img {
    width: 100%;
    height: 100%;
}
.St{
    width: 100%;
    height: 400px;
    color: var(--brown);
    align-items: center;
    z-index: 200;
    justify-content: space-between;
    position: relative;
    bottom: 170px;
}
.fSt{
   left: 10px;
   top: 270px;
   animation: mRight 6s linear;
   animation-delay: 1s;
   animation-iteration-count: infinite;
}
@keyframes mRight{
    0%{
        left: 0%;
        opacity: 1;
        transform: scale(1);
    }
    50%{
        top: 300px;
    }
    100%{
        top: 100px;
        left: 95%;
        opacity: 0.5;
        transform: scale(2);
    }
}
.stLeft ,.stRight{
    max-width: 250px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: url('../Images/ts/bg10.png') no-repeat;
    height: 250px;
    background-size: 100% 100%;
}
.stLeft{
    animation: flipLeft 2s linear;
}
.stRight{
    animation: flipRight 2s linear;
    animation-delay: 6s;
}
.stpDev{
    text-align: center;
}
.stpDev li{
    margin-top: 20px;
}
@keyframes flipLeft{
    0%{
        transform: rotate3d(0,1,0,10deg);
    }
   100% {
        transform: rotate3d(0,1,0,360deg);
    }
}
@keyframes flipRight{
    0%{
        transform: rotate3d(0,1,0,10deg);
    }
   100% {
        transform: rotate3d(0,1,0,360deg);
    }
}
@media only screen and (max-width: 800px) {
    .rdCircle{
        align-items: flex-start;
        text-align: center;
    }
}

@media only screen and (max-width:580px) {
    .stLeft ,.stRight{
        max-width: 100px;
        height: 100px;
    }
    h1{
        font-size: 1.8rem;
    }
    h2{
        font-size: 1.1rem;
    }
    .stLeft h2 ,.stRight h2{
        font-size: .7rem;
    }
    .rdCircle{
        align-items: center;
    }
}