.bBgimg{
    max-width: 1400px;
    width: 100%;
    height: 150vh;
    border: 1px solid black;
    background: url('../Images/ts/bg4.jpg') no-repeat;
    background-size: 100% 100%;
}
.fx{
    display: flex;
}
.bOuter{
    border: solid 0.4px var(--yellow);
    max-width: 700px;
    position: relative;
    top: 140px;
    margin: auto;
    z-index: 1;
    justify-content: center;
    align-items: center;
    height: 110vh;
    flex-direction: column;
    background: transparent ;
    border-radius:2%;
    animation: dropBx 1s linear;
    border-collapse: separate;
  
}
@keyframes dropBx{
    0%{
        top: 0px;
    }
    80%{
        top: 140px;
    }
    90%{
        top:130px;
    }
    100%{
        top: 140px;
    }
}
.bline{
    position: absolute;
    width: 30px;
    border-radius: 50px;
    top: 0;
    left: 50%;
    opacity: 0.4;
    height: 170px;
    border: solid 2px black;
    margin: auto;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    z-index: 2;
}
.bCircle1,.bCircle2{
    background-color: black;
    height: 20px;
    width: 20px;
    margin-top: 3px;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
}

.bCircle2{
    width: 10px;
    height: 10px;
    margin-top: 0px;
    background-color: white;
}
.Cbox{  
    max-width: 300px;
    padding-left: 10px;
    width: 100%;
    height: 100%;
    border-radius: 3px;
    align-items: center;
    font-size: 1.6rem;
    justify-content: center;
}
.Cbox a{
    color: rgba(255, 255, 255, 0.623);
}
.Clnk{
    text-decoration: none;
    cursor: pointer;
    letter-spacing: 2px;
    overflow: hidden;
}
.Cbox svg{
    color: rgb(248, 196, 83);
    color: linear-gradient(16deg, rgba(34,193,195,1) 20%, rgba(147,190,118,1) 41%, rgba(253,187,45,1) 69%);
    cursor: pointer;
}
.Cbox a:focus,.Cbox svg:focus{
    color: rgb(255, 255, 255);
}
.Cbox a:hover,.Cbox svg:hover {
    color: var(--blue);
}
.Cbox a:visited{
    color: var(--vblue);
}
.bInner{
    align-items: center;
    width: 100%;
    height: 50vh;
}
.cIcon{
    position: absolute;
    top: 30px;
    right: 30px;
    cursor: pointer;
}
.cIcon svg{
    font-size: 4rem;
    color: white;
}
.cIcon svg:hover{
    color: var(--blue);
}
@media (max-width:600px){
    .Cbox {
        font-size: 1rem;
    }
}