
.aOuter{
    background: var(--blue);
    height: 320vh;
    color: var(--brown);
    flex-direction: column;
    overflow: hidden;
}
.bgOuter{
    position: absolute;
    width: 100%;
    height: 930px;
    background: url('../Images/ts/bg6.png') no-repeat;
    background-size: 100% 100%;
}
.aOuter li svg{
    color: var(--yellow) !important;
    overflow: hidden;
    background-color: var(--vblue) !important;
}
.aInner{
    margin: auto;
    max-width: 1200px;
    height: 140vh;
    position: relative;
    top: 30px;
    padding: 10px;
    justify-content: space-around;
    width: 90%;
}
.innrImg{
    max-width: 300px;
    width: 100%;
    height: 400px;
    background: var(--vblue);
    border: solid 2px rgb(56 37 0);
    position: relative;
    border-radius: 5px;
    overflow: hidden;
    top: 15px;
}
.innrImg img {
    position: absolute;
    width: 100%;
    height: 130%;
    bottom: 0px;
    border-radius: 5px;
}
.innrImg img:hover {
    transform: scale(1.07);
    opacity: 0.7;
}
.innrTxt{
    position: relative;
    width: 50%;
    left: 40px;
}   
.aUl h2{    
    width   : 50%;
    font-size: 1rem;
    color: var(--brown) !important;
    justify-content: space-between;
}
.aUl svg{
    color: var(--yellow);
}
h3{
    margin-top: 10px;
    text-align: center;
    font-size: 1.12rem;
}
.aInner-1{
    width: 100%;
    height: 100vh;
    padding: 10px;
    position: relative;
    justify-content: space-around;
}
.innrTxt-1{
    max-width: 700px;
    width: 100%;
    padding-left: 20px;
    height: 100%;
}
.innrImg-1{
    max-width: 600px;
    width: 100%;
    height: 100%;
    background: url('../Images/ts/bg7.png') no-repeat;
    background-size: 118% 108%;
    animation: rtBox 0.5s ease-in;
    animation-delay: 3s;
}
@keyframes rtBox {
    0%{
        transform: rotate(20deg);
    }
    50%{
        transform: rotate(-60deg);
    }
    80%{
        transform: rotate(-20deg);
    }
    100%{
        transform: rotate(0deg);
    }
}
.innrImg-1 img{
    margin: auto;
    width: 44.3%;
    transform: rotate(-4deg);
    position: relative;
    left: 1px;
    bottom: 56px;
    height: 60%;
}
.innrTxt-1 li,.innrTxt li{
   display: flex;
   align-items: center;
   padding-top: 10px;
}
.innrTxt-1 li svg,.innrTxt li svg{
    margin:0px 15px;
}
.rOuter{
    width: 100%;
    background: rgb(228,73,141);
    background: radial-gradient(circle, rgba(228,73,141,1) 0%, rgba(89,103,215,1) 58%, rgba(56,137,232,1) 78%);
    height: 40px;
    margin-top: 20px;
    margin-bottom: 2px;
    align-items: center;
    justify-content: center;
}
@media only screen and (max-width : 1000px){
    
    .aOuter{
        height: 390vh;
    }
    .aInner-1{
        height: 120vh;
    }
    .bgOuter{
        height: 1400px;
    }
    .aInner{
        flex-wrap: wrap-reverse;
        top: 45px;
    }
    .innrTxt{
        width: 80%;
        text-align: center;
        left: 0px;
        top: 40px;
    }
    .innrImg{
        right: 0px;
    }
    .innrTxt h2{
        font-size: 1.5rem;
    }

}
@media only screen and (max-width:780px) {
    .aOuter{
        height: 500vh;
    }
    .aInner-1{
        height: 240vh;
        flex-wrap: wrap-reverse;
    }
    .innrImg-1{
        height: 40%;
    }
    .innrTxt-1{
        height: 60%;
    }

}
@media only screen and (max-width:620px){
    .aInner{
        top: 45px;
    }.aOuter{
        height: 575vh;
    }
    .innrImg{
        max-width: 215px;
        height: 300px;
        left: 25px;
        top: 30px;
    }
    .bgOuter{
        height: 1700px;
    }
    .innrTxt{
        top: 45px;
    }
    .innrTxt h2{
        font-size: 1.2rem;
    }
    .innrTxt h3{
        font-size: 1rem;
    }
}
@media only screen and (max-width:450px){
    .innrImg-1{
        background-size: 143% 105%;
    }
    .innrImg-1 img{
        width: 52.3%;
        left: 43px; 
    }
    .aInner{
        top: 95px;
    }
}
@media (max-width:400px){
    .innrImg-1 img{
        width: 51.3%;
        left: 30px;
    }
}
@media (max-width:300px){
    .innrImg-1 img{
        left: 25px;
    }
}