:root{
    --blue:rgb(34,193,195);
    --vblue:rgb(19, 136, 138);
    --yellow:rgba(253,187,45,1);
    --brown:rgb(56 37 0);
    --white : rgb(250 249 247) ;
}
body{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: rgb(0,0,0);
    color: white;
    overflow-x: hidden;
    /* background: rgb(225 219 213); */
}

.outer{
    height: auto;
    background-color: white;
    flex-direction: column;
    text-rendering: optimizeLegibility;
    background-color: rgb(0,0,0);
    color: white;
}
.Hline{
    width: 105%;
    border-bottom: solid 3px rgba(253,187,45,1);
    border-bottom: solid 3px var(--yellow);
    height: 90px;
    -webkit-animation:  DVline 2.5s linear;
            animation:  DVline 2.5s linear;
}
@-webkit-keyframes DVline{
    0%{
        width: 0%;
    }
    100%{
        width: 100%;
    }
}
@keyframes DVline{
    0%{
        width: 0%;
    }
    100%{
        width: 100%;
    }
}
.Vline{
    width: 20%;
    height: 110vh;
    position: absolute;
    top: 0;
    z-index: 300;
    left: 100px;
    border-left: solid 3px rgba(253,187,45,1);
    border-left: solid 3px var(--yellow);
    -webkit-animation: DHline 2.5s linear;
            animation: DHline 2.5s linear;
}
@-webkit-keyframes DHline{
    0%{
        height: 0%;
    }
    100%{
        height: 100%;
    }
}
@keyframes DHline{
    0%{
        height: 0%;
    }
    100%{
        height: 100%;
    }
}
.Hbox,.wave{
