.pprCpc,.pprCpc2{
    width: 100px;
    height: 100px;
    position: relative;
    box-sizing: border-box;
    background-color: var(--vblue);
    background: url(/static/media/paper.b0828d64.png) no-repeat;
    background-size: 100% 100%;
    transform: rotate(270deg);
}
.pprCpc{
    -webkit-animation: lMove 6s ease;
            animation: lMove 6s ease;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    z-index: 10;
}
.pprCpc2{
    left: 45%;
    top: 200px;
    -webkit-animation: lMove2 6s ease-out;
            animation: lMove2 6s ease-out;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}
@-webkit-keyframes lMove{
    0%{
        left: 0%;
        top: 20px;
    }
    20%{
        left: 43%;
        top: 20px;
        transform: rotate(0deg);
    }
    50%{
        left: 48%;
        top: 250px;
    }
    100%{
        top: 250px;
        left: 100%;
    }
}
@keyframes lMove{
    0%{
        left: 0%;
        top: 20px;
    }
    20%{
        left: 43%;
        top: 20px;
        transform: rotate(0deg);
    }
    50%{
        left: 48%;
        top: 250px;
    }
    100%{
        top: 250px;
        left: 100%;
    }
}
@-webkit-keyframes lMove2 {
    0%{
        top: 190px;
        left: 46%;
    }
    50%{
        left: 48%;
